export default {
	props: {
		navBarDates: Array,
		navBartSelect: {
			type: Number,
			default: 0
		}
	},
	data() {
		return {
			navBartSelectStr: "",
		}
	},
	methods: {
		navBarClick(index) {
			this.$emit("navBarClick", index);
			this.navBartSelectStr = `id${index}`;
			// console.log();
		}
	},
	watch: {
		navBartSelect(){
			this.navBartSelectStr = `id${this.navBartSelect}`;
		}
		
	}
}
